.card .btn{
  padding: 1rem 2.5rem;
  background-color: #445ae3;
  border-radius: 2rem;
  margin: 1rem;
  text-transform: uppercase;
  color: #eee;
  font-size: 1.4rem;
}

.card .btn:hover {
  transform: translateY(-2px);
  box-shadow: .5rem .5rem 2rem rgba(0,0,0, .2);
}

.card .btn:active {
  transform: translateY(0);
  box-shadow: none;
}
