@media screen and (min-width: 700px){
  .card-wrapper{
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    width: auto;
  }

  .card{
    margin:2rem;
    transition: transform .5s;
    width: 40rem;
  }

  @keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
    .card:nth-child(1) {
      animation: fadeIn .5s .5s backwards;
      margin-left: auto;
      width: auto;

    }
  .card:nth-child(2) {
    animation: fadeIn .5s .1s backwards;

  }

  .card:hover{
    transform: scale(1.05);
  }

  }
.table {
  border-collapse: collapse;
  /*border: 2px solid #ddd;*/
  display: block;
  position: relative;
  table-layout: fixed;
  width: max-content;
}

.tablehead {
  padding: 10px;
  text-align: center;
  background-color: #6667a9;
  color: white;
}
th {
  border: 1px solid #ddd;
  padding: 10px;
}
td {
  border: 1px solid #ddd;
  padding: 2px;
  text-decoration: none;
  color: black;
}

.tbody td{
  border: 1px solid #ddd;
  padding: 12px;
  text-decoration: none;
  color: black;
}







